import localFont from "next/font/local";
import { Analytics } from "@vercel/analytics/next";
import { Footer } from "~/components/ui/footer";
import { SpeedInsights } from "@vercel/speed-insights/next";
import { env } from "./env";
import "./globals.css";
import type { Metadata, Viewport } from "next";

export const metadata: Metadata = {
  metadataBase: new URL(
    env.ENVIRONMENT === "production" ?
      "https://statsify.net" :
      "http://localhost:5000"
  ),
  title: "Archie",
  description: "Archie is the largest Hypixel Discord bot for viewing your Hypixel Network stats, leaderboards, sessions, and more. Easily track stats in BedWars, SkyWars, Duels, and all other minigames to see your progression and improvement over time.",
  icons: "../public/logos/logo_64.png",
  creator: "Archie Team",
};

export const viewport: Viewport = {
  colorScheme: "dark",
  // Matches --color-blueify-500
  themeColor: "#1e86da",
};

const MinecraftFont = localFont({
  src: [
    { style: "Regular", weight: "400", path: "../public/fonts/Minecraft.ttf" },
    {
      style: "Bold",
      weight: "600",
      path: "../public/fonts/Minecraft Bold.ttf",
    },
    {
      style: "Italic",
      weight: "400",
      path: "../public/fonts/Minecraft Italic.ttf",
    },
    {
      style: "Italic",
      weight: "600",
      path: "../public/fonts/Minecraft Bold Italic.ttf",
    },
  ],
  preload: true,
  variable: "--font-minecraft",
});

export default function RootLayout({ children }: LayoutProps<"/">) {
  return (
    <html lang="en" suppressHydrationWarning>
      <body
        className={`${MinecraftFont.variable} antialiased leading-[normal] h-screen flex flex-col bg-blackify-950`}
      >
        {children}
        <Analytics />
        <SpeedInsights />
        <Footer />
      </body>
    </html>
  );
}
